// libs
import React from "react";

// components
import UsersListWithInfiniteScroll from "./UsersListWithInfiniteScroll";
import { Images } from "shared/images";
import CustomSearchBar from "components/atom/chatComponents/Search";

const SearchSidebar = ({
    handleFilterUsersByName = () => {},
    setSearchSidebarVisible = () => {},
    searchKeyword,
    searchedData = [],
    isLoading = false,
    isSearchBarActive = false,
    handleActiveUser = () => {},
    activeUser = {},
    fetchUsersByName = () => {},
    totalUsers = 0,
}) => {
    const handleEndReached = () => {
        if (searchedData.length < totalUsers) {
            fetchUsersByName();
        }
    };

  return (
    <>
      <div className="position-relative"> 
        <CustomSearchBar
          searchAPI={() => fetchUsersByName(true)}
          keyword={isSearchBarActive ? searchKeyword : null}
          handleChangeKeyword={handleFilterUsersByName}
          name={"search"}
          placeholder="Search here..."
          isSearchBarIcon={false}
          debounceTime={700}
          autoFocus={true}
          // onBlur={() =>  setSearchSidebarVisible(false)}
        />
        <span
          className="search_close"
          onClick={() => {
            setSearchSidebarVisible(false);
          }}
        >
          <img src={Images.close} alt="Close" width={16} height={16} />
        </span>
      </div>
      <div className="chat_title">
        <h6 className="mb-0">Inbox</h6>
      </div>
      <ul className="chat_list list-unstyled">
        {isLoading ? (
          "loading"
        ) : searchedData?.length > 0 ? (
          //list
          <UsersListWithInfiniteScroll
            // styles={{ height: 400 }}
            data={searchedData}
            endReached={handleEndReached}
            overscan={20}
            itemClick={handleActiveUser}
            activeUser={activeUser}
          />
        ) : (
          "No searched items"
        )}
      </ul>
    </>
  );
};

export default SearchSidebar;
