import React, { useCallback, useEffect, useMemo, useState } from "react";
import "./style.scss";
import debounce from "lodash.debounce";

function CustomSearchBar({
  placeholder= "Search here...",
  style,
  searchAPI = () => { },
  debounceTime = 300,
  keyword,
  handleChangeKeyword,
  isSearchBarIcon = true,
  ...rest
}) {

  const debouncedResults = useMemo(() => {
    return debounce(searchAPI, debounceTime);
  }, [keyword]);

  useEffect(() => {
    return () => {
      debouncedResults.cancel();
    };
  }, [debouncedResults]);

  useEffect(() => {
    if(keyword != null){
      debouncedResults(keyword);
    }
  }, [keyword]);

  return (
    <div className={`${isSearchBarIcon ? "searchbar ms-2" : "position-relative"} `}>
      <input
        style={style}
        key="search-bar"
        placeholder={placeholder}
        onChange={handleChangeKeyword}
        className="form-control bg-white"
        value={keyword}
        {...rest}
      />
    </div>
  );
}

export default CustomSearchBar;
