import React from "react";
import CustomModal from "../../atom/modal/index";
import ButtonGreen from "components/atom/button/ButtonGreen";
import { STATUS_OPTIONS } from "shared/constants";
import DocumentViewer from "components/atom/docViewer";

function DocumentViewerModal({
  viewDoc,
  setViewDoc,
  documentButtonClick,
  disableKey,
  setShowReasonModal,
  docURL,
  onLoadError,
  onLoad,
  modalOpacity,
}) {
  return (
    <>
      <CustomModal
        isOpen={viewDoc}
        className="customModal"
        modalOpacity={modalOpacity}
        handleToggle={() => setViewDoc((prev) => !prev)}
      >
        <DocumentViewer
          docURL={docURL}
          viewDoc={viewDoc}
          setViewDoc={setViewDoc}
          onLoadError={onLoadError}
          onLoad={onLoad}
        />

        <div className="d-flex justify-content-center btn_group mt-4">
          <ButtonGreen
            title={STATUS_OPTIONS.VERIFY.title}
            onClick={() => documentButtonClick(STATUS_OPTIONS.VERIFY.key)}
            disabled={disableKey === STATUS_OPTIONS.VERIFY.key}
          />
          <button
            className="btn btn-md btn-danger"
            onClick={() => {
              setShowReasonModal(true);
              setViewDoc(false);
            }}
            disabled={disableKey === STATUS_OPTIONS.UNVERIFY.key}
          >
            {STATUS_OPTIONS.UNVERIFY.title}
          </button>
        </div>
      </CustomModal>
    </>
  );
}

export default DocumentViewerModal;
