// Importing necessary dependencies
import React from "react";
import Slider from "rc-slider";
import "rc-slider/assets/index.css";

// Creating a functional component called RangeSlider that takes in two parameters as props: state and setState
function RangeSlider({ state, setState }) {
  // Initializing 'value' using useState hook with default value [20,37]
  const [value, setValue] = React.useState([20, 37]);

  // Handling change in value of the slider by updating the 'value' state and calling the setState function passed as prop
  const handleChange = (newValue) => {
    setValue(newValue);
    setState(newValue);
  };

  // Customizing tip formatter to display the current slider value inside the slider knob when dragged
  const handleTipFormatter = (value) => {
    return `${value}`;
  };

  // Rendering the range slider component inside a div with specific class name, passing various props for customizations
  return (
    <div className="d-flex form-control bg-white">
      <Slider
        range
        allowCross={false}
        min={0}
        max={100}
        value={state}
        onChange={handleChange}
        tipFormatter={handleTipFormatter}
      />
    </div>
  );
}

// Exporting the RangeSlider component wrapped with React.memo to optimize its performance by reducing unnecessary re-renders
export default React.memo(RangeSlider);
