import React, { useEffect, useState } from "react";
import useNotification from "components/atom/snackbar";
import { useCallback } from "react";
import { useDispatch } from "react-redux";
import { useHistory } from "react-router-dom";
import { getJobDetailsUser } from "redux/actions/auth";
import {
  SNACK_MESSAGES,
  SNACK_VARIANT,
  STATUS_OPTIONS,
} from "shared/constants";
import CustomTable from "components/atom/table";
import { getLocalizedDateString } from "components/utility";
import { Images } from "shared/images";
import ButtonStatus from "components/atom/button/ButtonStatus";
import Ratingstars from "components/atom/rating";

function JobTable({ id, navigateBranches = () => {} }) {
  const history = useHistory();
  const dispatch = useDispatch();
  const [msg, sendNotification] = useNotification();
  const [data, setData] = useState({});
  const [pageCount, setPageCount] = useState(1);
  const [keyword, setKeyword] = useState("");
  const [itemsPerPage, setItemsPerPage] = useState(15);

  const viewUser = useCallback(() => {
    let data = {
      user_id: id,
      page: pageCount,
      search: keyword,
      limit: itemsPerPage,
    };
    dispatch(
      getJobDetailsUser(data, (res) => {
        setData(res?.data);
      }),
      (err) => {
        sendNotification({
          msg: err?.data?.message
            ? err.data.message
            : SNACK_MESSAGES.OOPS_SOMETHING_WENT_WRONG,
          variant: SNACK_VARIANT.ERROR,
        });
        history.goBack();
      }
    );
  }, [id, dispatch, sendNotification, history]);

  const headerData = {
    ID: "#",
    JOB_ID: "Job ID",
    PHARMACY: "Pharmacy",
    BRANCH: "BRANCH",
    JOINED_DATE: "Joined Date",
    RATING: "Rating",
    RATE: "Rate",
    STATUS: "Status",
    ACTION: "Action",
  };

  useEffect(() => {
    viewUser();
  }, [viewUser]);

  return (
    <div className="tab_content">
      {data?.length ? (
        <CustomTable>
          <thead>
            <tr key={1000 + 1}>
              {Object.values(headerData).map((i, idx) => (
                <th scope="col" id={idx}>
                  {i}
                </th>
              ))}
            </tr>
          </thead>
          {data?.map((i, idx) => {
            return (
              <tbody>
                <tr>
                  <td>{idx + 1}</td>
                  <td>{i?.company_name}</td>
                  <td>{i?.job_title}</td>
                  <td>{getLocalizedDateString(i?.start_date)}</td>
                  <td>{getLocalizedDateString(i?.end_date)}</td>
                  <td>
                    <Ratingstars rating={3} />
                  </td>
                  <td>{`£20`}</td>
                  <td>
                    <div className="d-flex align-items-center">
                      <span
                        className={`m-1 badge-md badge-${
                          i?.verified === STATUS_OPTIONS.PENDING.key
                            ? "info"
                            : i?.verified === STATUS_OPTIONS.VERIFY.key
                            ? "success"
                            : i?.verified === STATUS_OPTIONS.UNVERIFY.key
                            ? "danger"
                            : "grey"
                        }`}
                      >
                        {`Status`}
                      </span>
                    </div>
                  </td>
                  <td>
                    <ButtonStatus
                      image={Images.viewIcon}
                      status={"default"}
                      title={"View Details"}
                      onClick={() => navigateBranches(i?.id)}
                    />
                  </td>
                </tr>
              </tbody>
            );
          })}
        </CustomTable>
      ) : (
        <div className="text-center no_result py-5">No Data Availiable</div>
      )}
    </div>
  );
}

export default JobTable;
