import CustomModal from "components/atom/modal";
import React from "react";
import { BUTTONS, STRINGS } from "shared/constants";
import { Images } from "shared/images";
import './style.scss';
import CustomButton from "../CustomButton";

function DeleteModal({ onClick, toggleModal, show, medium = false, text = "", buttonText = "",icon ="" }) {
  return (
    <>
      <CustomModal
        isOpen={show}
        className="as"
        avarge={medium ? false : true}
        medium={medium}
        handleToggle={toggleModal}
      >
        <div className="confirmation_modal text-center">
          <em className={`mb-3 modal_icon ${buttonText === "Block" || buttonText === "Delete Chat" ? "danger_icon" : "" }`}>
            <img src={icon?.length ? icon : Images.deleteIcon} alt="" width="30" />
          </em>
          <h2 className="h5 mb-2">{STRINGS.ARE_YOU_SURE}</h2>
          <p className="mb-4">{text || STRINGS.WANT_TO_DELETE}</p>
          <CustomButton
              className="btn btn-md btn-primary"
              title={buttonText}
              onClick={onClick}
          />
        </div>
      </CustomModal>
    </>
  );
}

export default DeleteModal;
